<template>
  <div id="datail">
    <DetailNavBar class="detail-nav"></DetailNavBar>
    <Scroll class="content">
    <!-- banner 图 -->
    <el-carousel height="350px">
      <el-carousel-item v-for="(item, index) in banners" :key="index" v-if="index < 7">
        <div id="bannerImg">
          <img :src="item"/>
        </div>
      </el-carousel-item>
    </el-carousel>
    <!-- 商品信息 -->
    <DetailBaseInfo :Goods='Goods' v-if="Goods"></DetailBaseInfo>
    </Scroll>
  </div>
</template>

<script>
import DetailNavBar from './childComponents/detailNavBar.vue'
import DetailBaseInfo from './childComponents/detailBaseInfo.vue'
import { getDetail , Goods } from '../../network/detail'
import Scroll from '../../components/commom/scroll/scroll.vue'


export default {
  props: {},
  name:'Detail',
  data() {
    return {
      iid: null,
      banners: [],
      Goods:null,
    }
  },
  created() {
    // 保存传入的id
    this.iid = this.$route.params.id
    getDetail(this.iid).then(res => {
      console.log(res);
      this.banners = []
      this.banners = res.result.itemInfo.topImages
      // 获取商品信息
      this.Goods = new Goods(res.result,res.result.columns,res.result.shopInfo.services)
      console.log(this.Goods);
    })
  },
  computed: {},
  mounted() {},
  methods: {
  },
  components: {
    DetailNavBar,
    DetailBaseInfo,
    Scroll
  }
}
</script>

<style scoped lang="less">
.el-carousel__item {
  color: #475669;
  font-size: 14px;
  opacity: 1;
}
.el-carousel__item img {
  width: 100%;
}
#datail{
  position: relative;
  z-index: 12;
  background-color: #fff;
  height: 100vh;
}
.content{
  height: calc(100% - 44px);
}
.detail-nav{
  position: relative;
  z-index: 11;
  background-color: #fff;
}
</style>
<!-- 为了控制element-ui 轮播图 的 button 的 长度  -->
<style>
.el-carousel__button{
 width: 16px;
}
</style>
