import { request } from './request'

export function getDetail (iid){
    return request({
        url: '/detail',
        params: {
         iid
        }
      })
}

export class Goods {
   constructor(itemInfo,columns,services){
     this.title = itemInfo.itemInfo.title
     this.desc = itemInfo.itemInfo.desc
     this.newPrice = itemInfo.itemInfo.price
     this.oldPrice = itemInfo.itemInfo.oldPrice
     this.discountDesc = itemInfo.itemInfo.discountDesc
     this.columns = columns
     this.services = services
     this.lowPrice = itemInfo.itemInfo.lowPrice
     this.shopLogo = itemInfo.shopInfo.shopLogo
     this.shopName = itemInfo.shopInfo.name
     this.shopUrl = itemInfo.shopInfo.shopUrl
     this.score = itemInfo.shopInfo.score
     this.cGoods = itemInfo.shopInfo.cGoods
     this.cSells = itemInfo.shopInfo.cSells
   }
}