<template>
  <div id="detailNavBar">
    <NavBar>
        <div slot="left" class="backImg" @click="backClick">
            <img src="../../../assets/img/common/back.svg" alt="">
        </div>
      <div slot="center" class="title">
        <div
          v-for="(item, index) in titles"
          :key="index"
          class="title-item"
          :class="{ active: index === currentIndex }"
          @click="itemClick(index)"
        >
          {{ item }}
        </div>
      </div>
    </NavBar>
  </div>
</template>

<script>
import NavBar from '../../../components/commom/navbar/NavBar.vue'

export default {
  props: {},
  data() {
    return {
      titles: ['商品', '参数', '评论', '推荐'],
      currentIndex: 0
    }
  },
  computed: {},
  mounted() {},
  methods: {
    itemClick(index) {
      this.currentIndex = index
    },
    backClick(){
        this.$router.back()
    }
  },
  components: {
    NavBar
  }
}
</script>

<style scoped lang="less">
.title {
  display: flex;
  font-size: 13px;
  line-height: 44px;
  margin-top: 4px;
}
.title-item {
  flex: 1;
}
.active {
  color: var(--color-high-text);
}
.backImg img{
    margin-top: 14px;
}
</style>
